import React, { Component } from 'react';
import Calendar from './Calendar/Calendar.js';

class PVECalendar extends Component {

  render() {
    return (
      <div className="PVECalendar">
        <Calendar color="Navy"/>
      </div>
    );
  }
}

export default PVECalendar;



